import _ from 'lodash';
import { z } from 'zod';

// Helper for clamped number stats (0-100)
const statSchema = z.coerce.number().transform(n => _.clamp(n, 0, 100));

// Helper for clamped relationship values (-100 to 100)
const relationValueSchema = z.coerce.number().transform(n => _.clamp(n, -100, 100));

export const Schema = z.object({
  主角: z
    .object({
      角色姓名: z.string().prefault('AI扮演的主角的姓名'),
      性别: z.string().prefault('无'),
      职业: z.string().prefault('无'),
      外貌特征: z
        .object({
          年龄: z.coerce.number().prefault(0),
          血型: z.string().prefault('无'),
          身高: z.coerce.number().prefault(0),
          体重: z.coerce.number().prefault(0),
          发色: z.string().prefault('无'),
          发型: z.string().prefault('无'),
          瞳色: z.string().prefault('无'),
          肤色: z.string().prefault('无'),
          整体印象: z.string().prefault('无'),
        })
        .prefault({}),
      基础属性: z
        .object({
          体力: statSchema.prefault(0),
          精力: statSchema.prefault(0),
          魅力: statSchema.prefault(0),
          学力: statSchema.prefault(0),
          财力: statSchema.prefault(0),
        })
        .prefault({}),
      当前状态: z
        .object({
          行为: z.string().prefault('无'),
          姿势: z.string().prefault('无'),
          所在地点: z.string().prefault('无'),
          所在场景: z.string().prefault('无'),
          着装: z
            .object({
              上装: z.string().prefault('无'),
              下装: z.string().prefault('无'),
              内衣: z.string().prefault('未知'),
              袜子: z.string().prefault('无'),
              鞋子: z.string().prefault('无'),
              饰品: z.string().prefault('无'),
            })
            .prefault({}),
        })
        .prefault({}),
      性格侧写: z
        .object({
          口头禅: z.string().prefault('无'),
          习惯动作: z.string().prefault('无'),
          爱好: z.string().prefault('无'),
          表性格: z.string().prefault('无'),
          里性格: z.string().prefault('无'),
          理性演绎: z.string().prefault('无'),
          内在本我: z.string().prefault('无'),
        })
        .prefault({}),
    })
    .prefault({}),

  人际关系: z
    .record(
      z.string().describe('{{user}}'), // Character Name as key
      z
        .object({
          好感度: relationValueSchema.prefault(0),
          信任度: relationValueSchema.prefault(0),
          过往遗憾: z.string().prefault('无'),
          评价: z.string().prefault('无'),
        })
        .prefault({}),
    )
    .prefault({}),

  世界: z
    .object({
      日期: z.string().prefault('2024-03-15'),
      时间: z.string().prefault('14:30'),
      天气: z.string().prefault('多云'),
      同场景: z.boolean().prefault(false),
    })
    .prefault({}),
});
